router.beforeEach((to, from, next) => {
      // console.log(to)
      // 消息界面验证
      var a = ["xiaoxi","pinglunqu","gouwuche","userSafe","fabu"]
      for(var i=0;i<a.length;i++){
         if (to.name == a[i]) {
            if(store.state.user.username == null){
              if(from.name == "login"){
                console.log("返回！")
              }else{
                alert("您还没有登录，点击确定前往登录界面！")
              }
              router.push("/login")
              next()
            }else{
              next()
            }
          } else {
           next()
        }
    }
    if(from.name == "login"){
      if(to.name == "gouwuche"){
        router.push("/home/index")
        next()
      }else{
        next()
      }
    }
})

export default router
